.register__page {
  padding: 15px 50px;
  background-image: linear-gradient(
      to right,
      rgba(47, 48, 58, 0.4),
      rgba(47, 48, 58, 0.4)
    ),
    url('./imageRegisterBack2.jpg');
  height: 550px;
  margin-left: auto;
  margin-right: auto;
  max-width: 600px;
}
.register__title {
  color: rgb(255, 145, 0);
  text-transform: uppercase;
}
.register__form {
  display: flex;
  flex-direction: column;
  gap: 20px;
}
.register__label {
  display: flex;
  flex-direction: column;

  max-width: 400px;
  font-size: 24px;
  font-weight: 600;

  color: rgb(247, 255, 255);
}
.register__input {
  padding: 15px;
  font-size: 24px;
  border: none;
  box-shadow: 0px 5px 5px rgba(0, 0, 0.5, 0.15),
    0px 1px 1px rgba(0, 0, 0.5, 0.2), 1px 2px 5px rgba(0, 0, 0, 0.5);
  border-radius: 20px;
  color: rgb(255, 145, 0);
}
.register__input:hover,
.register__input:focus {
  border: none;
  box-shadow: 5px 5px 20px 5px #75bfd8 inset;
}
.register__button {
  width: 150px;
  padding: 16px;
  border: none;
  font-size: 16px;
  font-weight: 600;
  background-color: rgb(255, 145, 0);
  color: rgb(248, 249, 250);
  cursor: pointer;
  border-radius: 50px;
}
.register__button:hover,
.register__button:focus {
  background-color: #0c0f63;
  color: white;
}
.register__tologin {
  color: rgb(255, 145, 0);
  font-size: 16px;
  text-decoration: none;
}
